import React, { Component } from 'react';
import styles from './InputFile.module.css';

class InputFile extends Component {
  constructor(props) {
    super(props);
    this.fileInput = React.createRef();
  }

  state = {
    fileName: null,
  };

  handleChange = event => {
    event.preventDefault();
    const file = this.fileInput.current.files[0];
    if (file) {
      this.props.form.setFieldValue('photo', file);
      this.setState({ fileName: file.name });
    } else {
      this.props.form.setFieldValue('photo', null);
      this.setState({ fileName: null });
    }
  };

  handleBlur = () => {
    this.props.onBlur('photo', true);
  };

  render() {
    console.log(this.props);
    const { fileName } = this.state;
    return (
      <>
        <p>Photo</p>
        {/* <label className={this.props.className} htmlFor="file"> */}
        <input
          className={styles.inputfile}
          onChange={this.handleChange}
          onBlur={this.handleBlur}
          id="file"
          type="file"
          ref={this.fileInput}
        />
        {/* </label> */}
        <label
          className={`${styles.customFile} ${this.props.className}`}
          tabIndex="0"
          htmlFor="file"
        >
          <span className={fileName ? styles.filename : styles.placeholder}>
            {fileName || 'Upload your photo'}
          </span>
          <span className={styles.browseButton}>Browse</span>
        </label>
      </>
    );
  }
}

export default InputFile;
