/* eslint-disable jsx-a11y/label-has-associated-control */
import React from 'react';
import { Formik, Form, Field } from 'formik';
import * as yup from 'yup';
import styles from './RegistrationForm.module.css';
import Button from '../Shared/Button/Button';
import InputFile from './InputFile/InputFile';

const positions = [
  {
    id: 1,
    name: 'Security',
  },
  {
    id: 2,
    name: 'Designer',
  },
  {
    id: 3,
    name: 'Content manager',
  },
  {
    id: 4,
    name: 'Lawyer',
  },
];

const phoneRegex = /^\+380\d{3}\d{2}\d{2}\d{2}$/;
const FILE_SIZE = 5 * 1024 * 1024;
const SUPPORTED_FORMATS = ['image/jpg', 'image/jpeg'];

const Schema = yup.object().shape({
  name: yup
    .string()
    .min(2, 'Name must be at least 2 characters')
    .max(60, 'Name must be at most 60 characters')
    .required('Name is required'),
  email: yup
    .string()
    .email('Email not valid')
    .required('Email is required'),
  phone: yup
    .string()
    .matches(phoneRegex, 'Phone not valid')
    .required('Phone is required'),
  position_id: yup.string().required('Position is required'),
  photo: yup
    .mixed()
    .required('Photo is required')
    .test(
      'fileSize',
      'File too large',
      value => value && value.size <= FILE_SIZE,
    )
    .test(
      'fileFormat',
      'Unsupported Format',
      value => value && SUPPORTED_FORMATS.includes(value.type),
    ),
});

const RegistrationForm = () => {
  return (
    <section>
      <div className="container">
        <h2 className={styles.title}>Register to get a work</h2>
        <Formik
          initialValues={{
            name: '',
            email: '',
            phone: '',
            position: '',
            photo: null,
            position_id: '',
          }}
          validationSchema={Schema}
          onSubmit={(values, { resetForm }) => {
            console.log(values);
            // const { onLogin } = this.props;
            // setTimeout(() => {
            //     onLogin(values);
            //     resetForm();
            // }, 1000);
          }}
        >
          {({ values, touched, errors, isSubmitting, setFieldTouched }) => {
            return (
              <Form className={styles.registration} autoComplete="off">
                <p className={styles.paragraph}>
                  Attention! After successful registration and alert, update the
                  list of users in the block from the top
                </p>
                <label htmlFor="name">Name</label>
                <Field
                  className={`${styles.input} ${
                    errors.name && touched.name ? styles.inputError : ''
                  }`}
                  type="text"
                  name="name"
                  placeholder="Your name"
                />
                {touched.name && errors.name && (
                  <p className={styles.error}>{errors.name}</p>
                )}
                <label htmlFor="email">Email</label>
                <Field
                  className={`${styles.input} ${
                    errors.email && touched.email ? styles.inputError : ''
                  }`}
                  type="email"
                  name="email"
                  placeholder="Your email"
                />
                {touched.email && errors.email && (
                  <p className={styles.error}>{errors.email}</p>
                )}
                <label htmlFor="phone">Phone</label>
                <Field
                  className={`${styles.input} ${
                    errors.phone && touched.phone ? styles.inputError : ''
                  }`}
                  type="tel"
                  name="phone"
                  placeholder="+380 XX XXX XX XX"
                />
                {!errors.phone || !touched.phone ? (
                  <p className={styles.assistive}>
                    Еnter phone number in open format
                  </p>
                ) : null}
                {touched.phone && errors.phone && (
                  <p className={styles.error}>{errors.phone}</p>
                )}
                <p>Select your position</p>
                {positions.map(el => (
                  <label
                    className={styles.checkContainer}
                    key={el.id}
                    htmlFor={el.id}
                  >
                    {el.name}
                    <Field
                      checked={+values.position_id === el.id}
                      type="radio"
                      id={el.id}
                      value={el.id}
                      name="position_id"
                    />
                    <span className={styles.checkmark} />
                  </label>
                ))}
                {touched.position_id && errors.position_id && (
                  <p className={styles.error}>{errors.position_id}</p>
                )}
                <Field
                  onBlur={setFieldTouched}
                  name="photo"
                  component={InputFile}
                  className={styles.inputFile}
                />
                {touched.photo && errors.photo && (
                  <p className={styles.error}>{errors.photo}</p>
                )}
                <Button type="submit" label="Sing up now" />
              </Form>
            );
          }}
        </Formik>
      </div>
    </section>
  );
};

export default RegistrationForm;
