import React, { Component, createRef } from 'react';
import PropTypes from 'prop-types';
import styles from './Navigation.module.css';

const mobileNavList = [
  'About me',
  'Relationships',
  'Users',
  'Sign Up',
  'Terms and Conditions',
  'How it works',
  'Partnership',
  'Help',
  'Leave testimonial',
  'Contact us',
  'Articles',
  'Our news',
  'Testimonials',
  'Licenses',
  'Privacy Policy',
];

class Navigation extends Component {
  static propTypes = {
    closeMenu: PropTypes.func.isRequired,
  };

  backdropRef = createRef();

  componentDidMount() {
    window.addEventListener('keydown', this.handleKeyPress);
    document.body.style.overflow = 'hidden';
  }

  componentWillUnmount() {
    window.removeEventListener('keydown', this.handleKeyPress);
    document.body.style.overflow = 'unset';
  }

  handleKeyPress = e => {
    if (e.code !== 'Escape') {
      return;
    }

    this.props.closeMenu();
  };

  handleBackdropClick = e => {
    if (this.backdropRef.current && e.target !== this.backdropRef.current) {
      return;
    }

    this.props.closeMenu();
  };

  render() {
    const mobileNavItems = mobileNavList.map((el, i) => (
      <a key={i} className={styles.navItem} href="/">
        {el}
      </a>
    ));
    return (
      <>

        <div className={styles.backdrop} onClick={this.handleBackdropClick}>
          <nav className={styles.nav}>{mobileNavItems}</nav>
        </div>
      </>
    );
  };
};

export default Navigation;
